* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
    color: inherit;
}
body {
    line-height: 1;
    color: #333;
}

.header {
    height: 100px;
}
.header .inner {
    max-width: 1280px;
    width: calc(100% - 30px);
    margin: 0 auto;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav_open {
    width: 20px;
    position: relative;
    z-index: 9;
}
.nav_open img {
    width: 100%;
}

.nav .gnb {
    display: flex;
    gap: 30px;
}

.all_nav {
    width: 50%;
    height: 100vh;
    position: fixed;
    top: 0;
    right: -100%;
    background-color: rgb(126, 115, 197);
    color: #fff;
    padding: 50px;
    transition: 0.5s linear;
}
.all_nav.on {
    right: 0;
}

.main_visual {
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.input_box {
    color: #fff;
    display: flex;
    align-items: center;
    gap: 10px;
}
.all_nav .input_box {
    flex-direction: column;
    margin: 50px 0;
    align-items: normal;
}
.input_box input {
    border: 1px solid #fff;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 25px;
    height: 25px;
}
.input_box input:focus {
    outline: 1px solid purple;
    color: purple;
}